<template>
  <div>
    <div class="container">
      <div class="empty-space col-xs-b15 col-sm-b30"/>
      <div class="breadcrumbs">
        <a href="#">home</a>
        <a href="#">shopping cart</a>
      </div>
      <div class="empty-space col-xs-b15 col-sm-b50 col-md-b100"/>
      <div class="text-center">
        <div class="simple-article size-3 grey uppercase col-xs-b5">shopping cart</div>
        <div class="h2">check your products</div>
        <div class="title-underline center"><span/></div>
      </div>
    </div>

    <div class="empty-space col-xs-b35 col-md-b70"/>

    <div class="container">
      <table class="cart-table">
        <thead>
          <tr>
            <th style="width: 95px;"/>
            <th>product name</th>
            <th style="width: 150px;">price</th>
            <th style="width: 260px;">quantity</th>
            <th style="width: 70px;">Code</th>
            <th style="width: 150px;">total</th>
            <th style="width: 70px;"/>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(cart,index) in carts" :key="cart._id">
            <td data-title=" ">
              <a class="cart-entry-thumbnail" href="#"><img src="/../../img//product-1.png" alt=""></a>
            </td>
            <td data-title=" "><h6 class="h6"><a href="#">{{ cart.productName }}</a></h6></td>
            <td data-title="Price: "> {{ cart.sellingPrice }}</td>
            <td>
              <div v-for="(qt,i) in quantity" :key="qt[index]">
              <div class="quantity-select" data-title="Quantity: " v-if="i === index ">
                <span class="minus" v-on:click="decreaseItem(index)"/>
                <span class="number">{{ qt }}</span>
                <span class="plus" v-on:click="increaseItem(index)"/>
              </div>
              </div>
            </td>
            <td data-title="Color: ">{{ cart.productCode }}</td>
            <td>
              <div v-for="(qt,ind) in quantity" :key="qt[index]">
              <span v-if="ind === index ">{{ cart.sellingPrice*qt }}</span>
              </div>
            </td>
            <td data-title="">
              <div class="btn btn-danger" v-on:click="removeItem(cart._id)">
                <span>X</span>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="empty-space col-xs-b35"/>
      <div class="row">
        <div class="col-sm-6 col-md-5 col-xs-b10 col-sm-b0">
          <div class="single-line-form">
            <input
              class="simple-input"
              type="text"
              value=""
              placeholder="Enter your coupon code" />
            <div class="button size-2 style-3">
              <span class="button-wrapper">
                <span class="icon"><img src="/../../img//icon-4.png" alt=""></span>
                <span class="text">submit</span>
              </span>
              <input type="submit" value="">
            </div>
          </div>
        </div>
        <div class="col-sm-6 col-md-7 col-sm-text-right">
          <div class="buttons-wrapper">
            <a class="button size-2 style-2" href="#">
              <span class="button-wrapper">
                <span class="icon"><img src="/../../img//icon-2.png" alt=""></span>
                <span class="text">update cart</span>
              </span>
            </a>
            <router-link class="button size-2 style-3" to="/checkout">
              <span class="button-wrapper">
                <span class="icon"><img src="/../../img//icon-4.png" alt=""></span>
                <span class="text">proceed to checkout</span>
              </span>
            </router-link>
          </div>
        </div>
      </div>
      <div class="empty-space col-xs-b35 col-md-b70"/>
      <div class="row">
        <div class="col-md-6 col-xs-b50 col-md-b0">
          <h4 class="h4 col-xs-b25">calculate shipping</h4>
          <select class="SlectBox">
            <option disabled="disabled" selected="selected">Choose country for shipping</option>
            <option value="volvo">Volvo</option>
            <option value="saab">Saab</option>
            <option value="mercedes">Mercedes</option>
            <option value="audi">Audi</option>
          </select>
          <div class="empty-space col-xs-b20"/>
          <div class="row m10">
            <div class="col-sm-6">
              <input
                class="simple-input"
                type="text"
                value=""
                placeholder="State / Country" />
              <div class="empty-space col-xs-b20"/>
            </div>
            <div class="col-sm-6">
              <input
                class="simple-input"
                type="text"
                value=""
                placeholder="Postcode / Zip" />
              <div class="empty-space col-xs-b20"/>
            </div>
          </div>
          <div class="button size-2 style-2">
            <span class="button-wrapper">
              <span class="icon"><img src="/../../img//icon-1.png" alt=""></span>
              <span class="text">update totals</span>
            </span>
            <input type="submit"/>
          </div>
        </div>
        <div class="col-md-6">
          <h4 class="h4">cart totals</h4>
          <div class="order-details-entry simple-article size-3 grey uppercase">
            <div class="row">
              <div class="col-xs-6">
                cart subtotal
              </div>
              <div class="col-xs-6 col-xs-text-right">
                <div class="color">$1195.00</div>
              </div>
            </div>
          </div>
          <div class="order-details-entry simple-article size-3 grey uppercase">
            <div class="row">
              <div class="col-xs-6">
                shipping and handling
              </div>
              <div class="col-xs-6 col-xs-text-right">
                <div class="color">free shipping</div>
              </div>
            </div>
          </div>
          <div class="order-details-entry simple-article size-3 grey uppercase">
            <div class="row">
              <div class="col-xs-6">
                order total
              </div>
              <div class="col-xs-6 col-xs-text-right">
                <div class="color">$1195.00</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="empty-space col-xs-b35 col-md-b70"/>
      <div class="empty-space col-xs-b35 col-md-b70"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'cart',
  created () {
      this.getCartItems();
      this.getQuantity();
  },
  data () {
      return {
          carts : [],
          quantity: [],
      }
  },
  methods : {
      getCartItems() {
          this.carts = this.$store.state.cartItems;
      },
      getQuantity() {
          this.quantity = this.$store.state.cartQuantity;
      },
      increaseItem(index) {
        this.$store.dispatch("increaseItem", index);
        this.getQuantity();
      },
      decreaseItem(index) {
        this.$store.dispatch("decreaseItem", index);
        this.getQuantity();
      },
      removeItem(id) {
        this.$store.dispatch("removeItem",id);
        this.getQuantity();
        this.getCartItems();
      }
  }

  
};
</script>


